@import './assets/core/resset';
@import './assets/core/mixins';
@import './assets/partials/_main';
@import './assets/partials/_aside';
@import './assets/partials/_character';
@import './assets/partials/_contact';
@import './assets/partials/_footer';
@import './assets/partials/_projects';
